.contactListTable {
  border-collapse: collapse;
  margin: 30px 0;
}

table td {
  padding: 6px 20px;
  border: 1px solid #2a2a2a;
}

tr:nth-child(even) {
  background-color: #f1eeee;
}

/* tr:hover {
  background-color: tomato;
  color: white;
} */

.button {
  margin: 0 8px;
  padding: 2px 15px;
  border-radius: 8px;
  font-size: 24px;
  cursor: pointer;
  background-color: #ffffff;
  transition: color, background-color 350ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button:hover {
  background-color: #98989a;
  color: #ffffff;
}
